lmente en las más vulnerables, la crianza se lleva a cabo en un entorno de soledad. Sin el acompañamiento adecuado de redes sociales, familiares o institucionales, los cuidadores a menudo repiten los mismos patrones de violencia que vivieron en su propia infancia, sin saber que existen alternativas más saludables.")])]),_c('div',{staticClass:"div",attrs:{"titulo":"Desconfianza hacia las instituciones"}},[_c('p',[_vm._v("Aunque existen rutas de protección para las víctimas de maltrato, muchas personas desconocen cómo activarlas, o temen represalias por denunciar. Esta desconfianza limita las oportunidades de intervención a tiempo y, en consecuencia, muchas víctimas no reciben la ayuda que necesitan.")])])])],1)]),_c('h2',{attrs:{"data-aos":"fade-left"}},[_vm._v("¿Qué consecuencias tiene el maltrato infantil?")]),_c('p',{staticClass:"mb-5"},[_vm._v("El maltrato no es solo un daño momentáneo, sino que deja huellas profundas en el desarrollo de los niños y niñas. Las consecuencias pueden ser físicas, emocionales y psicológicas:")]),_c('div',{staticClass:"bg-full-width bg-fondo-2 mb-5"},[_c('div',{staticClass:"p-4 p-md-5"},[_c('div',{staticClass:"row justify-content-center align-items-center"},[_c('div',{staticClass:"col-lg-10"},[_c('ImagenInfografica',{staticClass:"color-secundario",scopedSlots:_vm._u([{key:"imagen",fn:function(){return [_c('figure',[_c('img',{staticClass:"mx-auto",staticStyle:{"max-width":"1106px"},attrs:{"src":require("@/assets/curso/tema4/17.svg"),"alt":""}})])]},proxy:true}])},[_c('div',{staticClass:"bg-color-white box-shadow p-3",attrs:{"x":"16.5%","y":"66.5%","numero":"+"}},[_c('h5',[_vm._v("Problemas emocionales")]),_c('p',[_vm._v("La falta de cariño y el abuso físico o psicológico afectan el sentido de seguridad emocional de los niños, lo que puede dar lugar a problemas de autoestima, ansiedad y depresión.")])]),_c('div',{staticClass:"bg-color-white box-shadow p-3",attrs:{"x":"97%","y":"49%","numero":"+"}},[_c('h5',[_vm._v("Dificultades de apren